@mixin watch-round($watch-round) {
  width: $watch-round;
  height: $watch-round;
  border-radius: 50%;
}

@mixin min-round($min-round) {
  width: $min-round;
  height: $min-round;
  border-radius: 50%;
}

@mixin sec-round($sec-round) {
  width: $sec-round;
  height: $sec-round;
  border-radius: 50%;
}

@mixin center-round($center-round) {
  width: $center-round;
  height: $center-round;
  border-radius: 50%;
}
